sections.push(wrapContent(
  <>Un exemple en C++ compar  <i>TypeScript</i></>,
  'comparaison-cpp-ts',
  (Container) => (
    <Container>
      <p>
        Souvent, en C++, je me retrouvais  vouloir faire des choses du genre
        de ce qui suit&nbsp;:
      </p>
      <CodeSample language="cpp" title="Quelque chose qui ne marche pas en CPP">
        {`
          class Base {
            public:
            std::string type;
          };

          class A : public Base {
            public:

            A() {
              type = 'A';
            }
          };

          class B : public Base {
            public:
            std::string somethingOnlyBHas;

            B() {
              type = 'B';
              somethingOnlyBHas = "this is my secret message";
            }
          };

          void useInstance(Base *inst) {
            std::cout << "Instance type " << inst->type << std::endl;

            if (inst->type == "B") {
              // erreur : class "Base" has no member "somethingOnlyBHas"
              std::cout << inst->somethingOnlyBHas << std::endl;
            }
          }
        `}
      </CodeSample>
      <p>
        Il y a bien sr une solution, mais le problme est qu&apos;elle nous fait perdre
        toute l&apos;aide du compilateur.
      </p>
      <p>
        La solution est de brutalement caster <i>Base</i> vers <i>B</i>, ce qu&apos;on peut faire,
        mais qui nous fait perdre tout contrle du compilateur car le compilateur ne cherche
        pas  savoir si <i>inst</i> peut tre caste en <i>B</i>.
      </p>
      <CodeSample language="cpp" title="Le contournement qui marche en CPP">
        {`
          void useInstance(Base *inst) {
            std::cout << "Instance type " << inst->type << std::endl;

            if (inst->type == "B") {

              // Ici, le compilateur se fiche pas mal que
              // inst soit vraiment de type B, si jamais il y a d'autres
              // classes qui hritent de Base et qui ont le type B,
              // on court droit  la segfault
              B *b = (B *) inst;

              std::cout << b->somethingOnlyBHas << std::endl;
            }
          }
        `}
      </CodeSample>
      <p>Voil un quivalent qui marche et qui est sr en <i>TypeScript</i>&nbsp;:</p>
      <CodeSample language="typescript" title="Exemple de narrowing dans un cas d'hritage">
        {`
          interface Base {
            type: string
          }
          interface A extends Base {
            type: 'A'
          }

          interface B extends Base {
            type: 'B'
            somethingOnlyBHas: string
          }

          type ChildOfBase = A | B;

          const useInstance = (inst: ChildOfBase) => {
            if (inst.type === 'B') {
              console.log(inst.somethingOnlyBHas);
            }
          };
        `}
      </CodeSample>
    </Container>
  ),
));

